body {
  margin: 0;
  padding: 0;
  font: 13px/1.5 'Helvetica Neue', Arial, 'Liberation Sans', FreeSans, sans-serif;

  background: #292828;
  color: #DCDCDC;
}

html, body, #root {
  height: 100%;
  width: 100%;
}

h1, h2, h3, h4, h5, h6 {
  font-family: montserrat;
  font-weight: normal;
}

@-webkit-keyframes moveUp {
  from { -webkit-transform: translateY(calc(100% + 25px)); transform: translateY(calc(100% + 25px)); }
  to { -webkit-transform: translateY(0); transform: translateY(0); }
}

@keyframes moveUp {
  from { -webkit-transform: translateY(calc(100% + 25px)); transform: translateY(calc(100% + 25px)); }
  to { -webkit-transform: translateY(0); transform: translateY(0); }
}

@-webkit-keyframes moveDown {
  from { -webkit-transform: translateY(0); transform: translateY(0); }
  to { -webkit-transform: translateY(calc(100% + 25px)); transform: translateY(calc(100% + 25px)); }
}

@keyframes moveDown {
  from { -webkit-transform: translateY(0); transform: translateY(0); }
  to { -webkit-transform: translateY(calc(100% + 25px)); transform: translateY(calc(100% + 25px)); }
}

.service-worker-toast {
  background: white;
  border-radius: 5px;
  -webkit-box-shadow: 0 0 12px #181717;
          box-shadow: 0 0 12px #181717;
  color: #797979;
  padding: 17px;

  position: fixed;
  bottom: 25px;
  left: 25px;
  z-index: 10;

  -webkit-transition: -webkit-transform 0.3s ease;

  transition: -webkit-transform 0.3s ease;

  -o-transition: transform 0.3s ease;

  transition: transform 0.3s ease;

  transition: transform 0.3s ease, -webkit-transform 0.3s ease;
}

.service-worker-toast.service-worker-toast--enter {
  -webkit-animation: moveUp 0.5s ease;
          animation: moveUp 0.5s ease;
}
.service-worker-toast.service-worker-toast--exit {
  -webkit-animation: moveDown 0.5s ease;
          animation: moveDown 0.5s ease;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;  
}



